import React, { useState, useEffect } from 'react';
import { Link } from 'react-router-dom';
import { useAppSelector } from '../store/hooks';
import { Container, Table, Alert, Spinner, Badge } from 'react-bootstrap';
import { api } from '../api';

interface Order {
  id: number;
  status: string;
  creation_date: string;
  isotope_items_count: number;
  total_activity: number | null;
}

const OrdersList: React.FC = () => {
  const [orders, setOrders] = useState<Order[]>([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState('');
  
  const { isAuthenticated } = useAppSelector((state) => state.user);
  
  useEffect(() => {
    if (!isAuthenticated) {
      return;
    }
    
    loadOrders();
  }, [isAuthenticated]);
  
  const loadOrders = async () => {
    try {
      setLoading(true);
      const response = await api.isotopeOrders.isotopeOrdersMyOrders();
      
      //   API
      const apiOrders = Array.isArray(response.data) ? response.data : [];
      const transformedOrders: Order[] = apiOrders.map((order: any) => ({
        id: order.id || 0,
        status: order.status || 'DRAFT',
        creation_date: order.creation_date || new Date().toISOString(),
        isotope_items_count: order.isotope_items?.length || 0,
        total_activity: order.total_activity || null
      }));
      
      setOrders(transformedOrders);
      
    } catch (err: any) {
      console.error('   :', err);
      setError(` : ${err.response?.data?.detail || err.message}`);
    } finally {
      setLoading(false);
    }
  };
  
  const getStatusBadge = (status: string) => {
    const statusMap: { [key: string]: { variant: string, text: string } } = {
      'DRAFT': { variant: 'warning', text: '' },
      'FORMED': { variant: 'info', text: '' },
      'COMPLETED': { variant: 'success', text: '' },
      'REJECTED': { variant: 'danger', text: '' },
      'DELETED': { variant: 'secondary', text: '' },
    };
    
    const statusInfo = statusMap[status] || { variant: 'secondary', text: status };
    return <Badge bg={statusInfo.variant}>{statusInfo.text}</Badge>;
  };
  
  const formatDate = (dateString: string) => {
    return new Date(dateString).toLocaleDateString('ru-RU', {
      day: '2-digit',
      month: '2-digit',
      year: 'numeric',
      hour: '2-digit',
      minute: '2-digit'
    });
  };
  
  if (!isAuthenticated) {
    return (
      <Container className="mt-5">
        <Alert variant="warning">
              
        </Alert>
      </Container>
    );
  }
  
  if (loading) {
    return (
      <Container className="mt-5 text-center">
        <Spinner animation="border" variant="primary" />
        <p className="mt-3"> ...</p>
      </Container>
    );
  }
  
  return (
    <Container className="mt-4">
      <h2 className="mb-4" style={{ color: '#0B2149' }}>
        <i className="fas fa-list me-2"></i>
            
      </h2>
      
      {error && <Alert variant="danger">{error}</Alert>}
      
      {orders.length === 0 ? (
        <Alert variant="info">
          <i className="fas fa-info-circle me-2"></i>
              . <Link to="/isotopes">  </Link>
        </Alert>
      ) : (
        <Table responsive hover className="mt-4">
          <thead style={{ backgroundColor: '#0B2149', color: 'white' }}>
            <tr>
              <th>ID</th>
              <th> </th>
              <th></th>
              <th> </th>
              <th> </th>
              <th></th>
            </tr>
          </thead>
          <tbody>
            {orders.map(order => (
              <tr key={order.id}>
                <td>
                  <strong>#{order.id}</strong>
                </td>
                <td>{formatDate(order.creation_date)}</td>
                <td>{getStatusBadge(order.status)}</td>
                <td>
                  <Badge bg="primary">{order.isotope_items_count}</Badge>
                </td>
                <td>
                  {order.total_activity ? 
                    `${order.total_activity.toFixed(2)} ` : 
                    <span className="text-muted"> </span>
                  }
                </td>
                <td>
                  <Link 
                    to={`/orders/${order.id}`}
                    className="btn btn-sm btn-outline-primary"
                  >
                    <i className="fas fa-eye me-1"></i>
                    
                  </Link>
                </td>
              </tr>
            ))}
          </tbody>
        </Table>
      )}
      
      <div className="mt-4">
        <Link to="/isotopes" className="btn btn-primary">
          <i className="fas fa-plus me-2"></i>
            
        </Link>
      </div>
    </Container>
  );
};

export default OrdersList;