// src/features/filters/filtersSlice.ts
import { createSlice, PayloadAction } from '@reduxjs/toolkit';

//    - 
export interface FiltersState {
  name: string;
  decayType: string;
  halfLifeMin: number | null;
  halfLifeMax: number | null;
  atomicMassMin: number | null;
  atomicMassMax: number | null;
  isActive: boolean | null;
}

//  
const initialState: FiltersState = {
  name: '',
  decayType: '',
  halfLifeMin: null,
  halfLifeMax: null,
  atomicMassMin: null,
  atomicMassMax: null,
  isActive: null,
};

const filtersSlice = createSlice({
  name: 'filters',
  initialState,
  reducers: {
    //    
    setNameFilter: (state, action: PayloadAction<string>) => {
      state.name = action.payload;
    },
    
    //     
    setDecayTypeFilter: (state, action: PayloadAction<string>) => {
      state.decayType = action.payload;
    },
    
    //     
    setHalfLifeFilter: (state, action: PayloadAction<{ min: number | null; max: number | null }>) => {
      state.halfLifeMin = action.payload.min;
      state.halfLifeMax = action.payload.max;
    },
    
    //     
    setAtomicMassFilter: (state, action: PayloadAction<{ min: number | null; max: number | null }>) => {
      state.atomicMassMin = action.payload.min;
      state.atomicMassMax = action.payload.max;
    },
    
    //    
    setIsActiveFilter: (state, action: PayloadAction<boolean | null>) => {
      state.isActive = action.payload;
    },
    
    //   
    clearFilters: () => initialState,
    
    //   
    resetNameFilter: (state) => {
      state.name = '';
    },
    
    resetDecayTypeFilter: (state) => {
      state.decayType = '';
    },
    
    resetHalfLifeFilter: (state) => {
      state.halfLifeMin = null;
      state.halfLifeMax = null;
    },
    
    resetAtomicMassFilter: (state) => {
      state.atomicMassMin = null;
      state.atomicMassMax = null;
    },
  },
});

//  
export const {
  setNameFilter,
  setDecayTypeFilter,
  setHalfLifeFilter,
  setAtomicMassFilter,
  setIsActiveFilter,
  clearFilters,
  resetNameFilter,
  resetDecayTypeFilter,
  resetHalfLifeFilter,
  resetAtomicMassFilter,
} = filtersSlice.actions;

//  
export default filtersSlice.reducer;