// src/pages/Home.tsx
import React, { useState, useEffect, useRef } from 'react';
import { Carousel, Spinner } from 'react-bootstrap';
import { isotopeService } from '../services/api';

const Home: React.FC = () => {
  const [statistics, setStatistics] = useState<{
    total: number;
    longestLiving: { name: string; half_life: number };
    shortestLiving: { name: string; half_life: number };
    decayTypes: { [key: string]: number };
  } | null>(null);
  const [loading, setLoading] = useState(true);
  const [buttonColor, setButtonColor] = useState<string>('#14BF96');
  
  const isMounted = useRef(false);
  const updateCount = useRef(0);

  //  
  useEffect(() => {
    const loadStatistics = async () => {
      try {
        console.log('  ...');
        const isotopes = await isotopeService.getIsotopes();
        console.log(' :', isotopes);
        
        if (isotopes.length === 0) {
          console.log('  ');
          setStatistics(null);
          setLoading(false);
          return;
        }

        let longestLiving = isotopes[0];
        let shortestLiving = isotopes[0];
        const decayTypes: { [key: string]: number } = {};

        isotopes.forEach(iso => {
          if (iso.half_life > longestLiving.half_life) {
            longestLiving = iso;
          }
          if (iso.half_life < shortestLiving.half_life) {
            shortestLiving = iso;
          }
          decayTypes[iso.decay_type] = (decayTypes[iso.decay_type] || 0) + 1;
        });

        const topDecayTypes = Object.entries(decayTypes)
          .sort((a, b) => b[1] - a[1])
          .slice(0, 3);

        console.log(' :', {
          total: isotopes.length,
          longestLiving: longestLiving.name,
          shortestLiving: shortestLiving.name,
          decayTypes: Object.fromEntries(topDecayTypes)
        });

        setStatistics({
          total: isotopes.length,
          longestLiving: { 
            name: longestLiving.name, 
            half_life: longestLiving.half_life 
          },
          shortestLiving: { 
            name: shortestLiving.name, 
            half_life: shortestLiving.half_life 
          },
          decayTypes: Object.fromEntries(topDecayTypes)
        });
      } catch (error) {
        console.error('  :', error);
      } finally {
        setLoading(false);
      }
    };

    loadStatistics();
  }, []);

  //      -   
  useEffect(() => {
    if (!isMounted.current) {
      //  
      isMounted.current = true;
      console.log(' Home .   :', buttonColor);
    } else {
      //  
      updateCount.current += 1;
      const newColor = updateCount.current % 2 === 1 ? '#0B2149' : '#14BF96';
      setButtonColor(newColor);
      console.log(` Home .   :`, newColor);
    }
  }, [statistics, loading]);

  const formatNumber = (num: number): string => {
    if (num >= 1e9) {
      return `${(num / 1e9).toFixed(2)} `;
    } else if (num >= 1e6) {
      return `${(num / 1e6).toFixed(2)} `;
    } else if (num >= 1e3) {
      return `${(num / 1e3).toFixed(2)} `;
    }
    return num.toFixed(2);
  };

  if (loading) {
    return (
      <div className="container">
        <div style={{ textAlign: 'center', padding: '60px' }}>
          <Spinner animation="border" variant="primary" />
          <p style={{ marginTop: '20px' }}> ...</p>
        </div>
      </div>
    );
  }

  return (
    <div className="container">
      <div style={{ textAlign: 'center', marginTop: '40px' }}>
        <p style={{ fontSize: '18px', marginBottom: '30px' }}>
                  
             .
        </p>

        {statistics ? (
          <div style={{ maxWidth: '800px', margin: '0 auto 40px auto' }}>
            <Carousel 
              indicators={true} 
              controls={true}
              prevIcon={
                <span 
                  aria-hidden="true" 
                  style={{
                    color: 'white',
                    fontSize: '24px',
                    backgroundColor: buttonColor,
                    borderRadius: '50%',
                    width: '40px',
                    height: '40px',
                    display: 'flex',
                    alignItems: 'center',
                    justifyContent: 'center'
                  }}
                >
                  
                </span>
              }
              nextIcon={
                <span 
                  aria-hidden="true" 
                  style={{
                    color: 'white',
                    fontSize: '24px',
                    backgroundColor: buttonColor,
                    borderRadius: '50%',
                    width: '40px',
                    height: '40px',
                    display: 'flex',
                    alignItems: 'center',
                    justifyContent: 'center'
                  }}
                >
                  
                </span>
              }
            >
              <Carousel.Item>
                <div style={{ 
                  height: '180px', 
                  backgroundColor: '#0B2149',
                  display: 'flex', 
                  alignItems: 'center', 
                  justifyContent: 'center',
                  color: 'white',
                  borderRadius: '8px',
                  padding: '20px'
                }}>
                  <div style={{ textAlign: 'center' }}>
                    <h3 style={{ fontSize: '20px', marginBottom: '15px', color: 'white' }}>
                        
                    </h3>
                    <p style={{ fontSize: '36px', fontWeight: 'bold', margin: 0, color: '#14BF96' }}>
                      {statistics.total}
                    </p>
                  </div>
                </div>
              </Carousel.Item>

              <Carousel.Item>
                <div style={{ 
                  height: '180px', 
                  backgroundColor: '#0B2149',
                  display: 'flex', 
                  alignItems: 'center', 
                  justifyContent: 'center',
                  color: 'white',
                  borderRadius: '8px',
                  padding: '20px'
                }}>
                  <div style={{ textAlign: 'center' }}>
                    <h3 style={{ fontSize: '18px', marginBottom: '10px', color: 'white' }}>
                        
                    </h3>
                    <p style={{ fontSize: '16px', fontWeight: 'bold', margin: '8px 0', color: '#14BF96' }}>
                      {statistics.longestLiving.name}
                    </p>
                    <p style={{ fontSize: '14px', margin: 0, color: '#ccc' }}>
                       : {formatNumber(statistics.longestLiving.half_life)} 
                    </p>
                  </div>
                </div>
              </Carousel.Item>

              <Carousel.Item>
                <div style={{ 
                  height: '180px', 
                  backgroundColor: '#0B2149',
                  display: 'flex', 
                  alignItems: 'center', 
                  justifyContent: 'center',
                  color: 'white',
                  borderRadius: '8px',
                  padding: '20px'
                }}>
                  <div style={{ textAlign: 'center' }}>
                    <h3 style={{ fontSize: '18px', marginBottom: '10px', color: 'white' }}>
                        
                    </h3>
                    <p style={{ fontSize: '16px', fontWeight: 'bold', margin: '8px 0', color: '#14BF96' }}>
                      {statistics.shortestLiving.name}
                    </p>
                    <p style={{ fontSize: '14px', margin: 0, color: '#ccc' }}>
                       : {statistics.shortestLiving.half_life.toFixed(2)} 
                    </p>
                  </div>
                </div>
              </Carousel.Item>

              <Carousel.Item>
                <div style={{ 
                  height: '180px', 
                  backgroundColor: '#0B2149',
                  display: 'flex', 
                  alignItems: 'center', 
                  justifyContent: 'center',
                  color: 'white',
                  borderRadius: '8px',
                  padding: '20px'
                }}>
                  <div style={{ textAlign: 'center' }}>
                    <h3 style={{ fontSize: '18px', marginBottom: '15px', color: 'white' }}>
                       
                    </h3>
                    <div style={{ display: 'flex', gap: '25px', justifyContent: 'center' }}>
                      {Object.entries(statistics.decayTypes).map(([type, count]) => (
                        <div key={type} style={{ textAlign: 'center' }}>
                          <div style={{ fontSize: '20px', fontWeight: 'bold', color: '#14BF96' }}>
                            {count}
                          </div>
                          <div style={{ fontSize: '12px', color: '#ccc' }}>
                            {type}
                          </div>
                        </div>
                      ))}
                    </div>
                  </div>
                </div>
              </Carousel.Item>
            </Carousel>
          </div>
        ) : (
          <div style={{ textAlign: 'center', padding: '40px', color: '#666' }}>
            <p>    </p>
          </div>
        )}

        <div style={{ marginTop: '40px', maxWidth: '800px', margin: '0 auto' }}>
          <h3 style={{ color: '#0B2149', marginBottom: '20px' }}> </h3>
          <p style={{ fontSize: '16px', lineHeight: '1.6', textAlign: 'left' }}>
                   . 
                    
                 .
          </p>
        </div>
      </div>
    </div>
  );
};

export default Home;