// src/features/orders/ordersSlice.ts
import { createSlice, createAsyncThunk, PayloadAction } from '@reduxjs/toolkit';
import { api } from '../../api';
import { IsotopeCalculationOrder, IsotopeCalculationItem, Isotope } from '../../api/Api';

//     
export interface IsotopeItem {
  id: number;
  isotope: number; //  ID 
  isotope_details?: Isotope; //   ( )
  initial_amount: number;
  remaining_activity: number | null;
  isotope_order: number;
}

export interface CustomOrder {
  id: number;
  status: 'DRAFT' | 'FORMED' | 'COMPLETED' | 'REJECTED' | 'DELETED';
  creation_date: string;
  formation_date: string | null;
  completion_date: string | null;
  client: number; //  ID 
  moderator: number | null; //  ID 
  time_elapsed: number;
  total_activity: number | null;
  isotope_items: IsotopeItem[];
}

//    - 
export interface OrdersState {
  orders: CustomOrder[];
  draftOrder: CustomOrder | null;
  loading: boolean;
  error: string | null;
}

//  
const initialState: OrdersState = {
  orders: [],
  draftOrder: null,
  loading: false,
  error: null,
};

// ==========   (THUNKS) ==========

// 1.   
export const fetchUserOrders = createAsyncThunk(
  'orders/fetchUserOrders',
  async (_, { rejectWithValue }) => {
    try {
      console.log('  ');
      
      //    API
      const response = await api.isotopeOrders.isotopeOrdersMyOrders();
      
      console.log('   ():', response.data);
      
      //   API   
      const apiOrders = response.data as unknown as IsotopeCalculationOrder[];
      const customOrders: CustomOrder[] = apiOrders.map((order: IsotopeCalculationOrder) => ({
        id: order.id || 0,
        status: order.status || 'DRAFT',
        creation_date: order.creation_date || new Date().toISOString(),
        formation_date: order.formation_date || null,
        completion_date: order.completion_date || null,
        client: typeof order.client === 'object' && order.client !== null ? order.client.id || 0 : 0,
        moderator: typeof order.moderator === 'object' && order.moderator !== null ? order.moderator.id || null : null,
        time_elapsed: order.time_elapsed || 0,
        total_activity: order.total_activity || null,
        isotope_items: (order.isotope_items || []).map((item: IsotopeCalculationItem) => ({
          id: item.id || 0,
          isotope: typeof item.isotope === 'object' && item.isotope !== null ? item.isotope.id || 0 : item.isotope || 0,
          isotope_details: typeof item.isotope === 'object' ? item.isotope : undefined,
          initial_amount: item.initial_amount || 1.0,
          remaining_activity: item.remaining_activity || null,
          isotope_order: item.isotope_order || order.id || 0,
        }))
      }));
      
      return customOrders;
    } catch (error: any) {
      console.error('  :', error);
      return rejectWithValue('   ');
    }
  }
);

// 2.   
