// src/features/user/userSlice.ts
import { createSlice, createAsyncThunk, PayloadAction } from '@reduxjs/toolkit';
import { api } from '../../api';
import { User } from '../../api/Api';

//   
export interface UserState {
  user: User | null;
  isAuthenticated: boolean;
  loading: boolean;
  error: string | null;
}

//  
const initialState: UserState = {
  user: null,
  isAuthenticated: false,
  loading: false,
  error: null,
};

// ==========   (THUNKS) ==========

// 1.  
export const loginUserAsync = createAsyncThunk(
  'user/loginUserAsync',
  async (credentials: { email: string; password: string }, { rejectWithValue }) => {
    try {
      console.log('   :', credentials.email);
      
      const response = await api.auth.authLoginCreate(credentials);
      
      console.log('   ():', response.data);
      
      // API  { message: "...", user: {...} }
      const responseData = response.data as any;
      
      let userData: User;
      
      if (responseData.user) {
        //    user
        userData = responseData.user as User;
      } else if (responseData.id) {
        //    
        userData = responseData as User;
      } else {
        //  
        userData = {
          id: 1,
          username: credentials.email.split('@')[0],
          email: credentials.email,
          first_name: '',
          last_name: '',
          is_staff: false
        };
      }
      
      //   localStorage
      localStorage.setItem('user', JSON.stringify(userData));
      localStorage.setItem('token', 'dummy-token');
      
      return userData;
    } catch (error: any) {
      console.error(' :', error.response?.data || error.message);
      return rejectWithValue(
        error.response?.data?.error || error.response?.data?.detail || error.message || ' email  '
      );
    }
  }
);

// 2.  
export const registerUserAsync = createAsyncThunk(
  'user/registerUserAsync',
  async (userData: { username: string; email: string; password: string }, { rejectWithValue }) => {
    try {
      console.log('  :', userData);
      
      const response = await api.users.usersRegister(userData);
      
      console.log('   ():', response.data);
      
      const responseData = response.data as any;
      let userToSave: User;
      
      if (responseData.user) {
        userToSave = responseData.user as User;
      } else if (responseData.id) {
        userToSave = responseData as User;
      } else {
        userToSave = {
          id: Date.now(),
          username: userData.username,
          email: userData.email,
          first_name: '',
          last_name: '',
          is_staff: false
        };
      }
      
      //    localStorage
      localStorage.setItem('user', JSON.stringify(userToSave));
      localStorage.setItem('token', 'dummy-token');
      
      return userToSave;
    } catch (error: any) {
      console.error(' :', error.response?.data || error.message);
      return rejectWithValue(
        error.response?.data?.error || error.response?.data?.detail || error.message || ' '
      );
    }
  }
);

// 3.    ( )
export const logoutUserAsync = createAsyncThunk(
  'user/logoutUserAsync',
  async (_, { rejectWithValue }) => {
    try {
      console.log('    ');
      
      try {
        const response = await api.auth.authLogoutCreate();
        console.log('API   :', response.data);
      } catch (apiError: any) {
        console.warn('API    CSRF :', apiError.response?.data || apiError.message);
        //     -  
      }
      
      //   localStorage
      localStorage.removeItem('user');
      localStorage.removeItem('token');
      
      console.log('localStorage ');
      
      return null;
    } catch (error: any) {
      console.error('   :', error);
      
      //     localStorage
      localStorage.removeItem('user');
      localStorage.removeItem('token');
      
      return rejectWithValue('    ');
    }
  }
);
// 4.     
export const fetchCurrentUserAsync = createAsyncThunk(
  'user/fetchCurrentUserAsync',
  async (_, { rejectWithValue }) => {
    try {
      console.log('    ');
      
      const response = await api.users.usersMe();
      
      console.log('   ( ):', response.data);
      
      const userData = response.data as User;
      
      if (!userData || !userData.id) {
        throw new Error('  ');
      }
      
      return userData;
    } catch (error: any) {
      console.error('  :', error.response?.data || error.message);
      return rejectWithValue('    ');
    }
  }
);

// ==========  ==========
const userSlice = createSlice({
  name: 'user',
  initialState,
  reducers: {
    //  
    clearUserError: (state) => {
      state.error = null;
    },
    
    //    localStorage   
    setUserFromStorage: (state) => {
      const storedUser = localStorage.getItem('user');
      if (storedUser) {
        try {
          const parsedUser = JSON.parse(storedUser);
          // ,    
          if (parsedUser && typeof parsedUser === 'object' && 'username' in parsedUser) {
            state.user = parsedUser as User;
            state.isAuthenticated = true;
            console.log('   localStorage:', state.user.username);
          }
        } catch (error) {
          console.error('    localStorage:', error);
          localStorage.removeItem('user');
        }
      }
    },
    
    //   
    clearUser: (state) => {
      state.user = null;
      state.isAuthenticated = false;
      localStorage.removeItem('user');
      localStorage.removeItem('token');
    },
    
    //   
    updateUserProfile: (state, action: PayloadAction<Partial<User>>) => {
      if (state.user) {
        state.user = { ...state.user, ...action.payload };
        localStorage.setItem('user', JSON.stringify(state.user));
      }
    },
  },
  
  //   
  extraReducers: (builder) => {
    builder
      // ========== LOGIN ==========
      .addCase(loginUserAsync.pending, (state) => {
        state.loading = true;
        state.error = null;
      })
      .addCase(loginUserAsync.fulfilled, (state, action) => {
        console.log('userSlice: loginUserAsync.fulfilled - :', action.payload.username);
        state.loading = false;
        state.isAuthenticated = true;
        state.user = action.payload;
        state.error = null;
      })
      .addCase(loginUserAsync.rejected, (state, action) => {
        console.log('userSlice: loginUserAsync.rejected - :', action.payload);
        state.loading = false;
        state.isAuthenticated = false;
        state.user = null;
        state.error = action.payload as string;
      })
      
      // ========== REGISTER ==========
      .addCase(registerUserAsync.pending, (state) => {
        state.loading = true;
        state.error = null;
      })
      .addCase(registerUserAsync.fulfilled, (state, action) => {
        console.log('userSlice: registerUserAsync.fulfilled - :', action.payload.username);
        state.loading = false;
        state.isAuthenticated = true;
        state.user = action.payload;
        state.error = null;
      })
      .addCase(registerUserAsync.rejected, (state, action) => {
        console.log('userSlice: registerUserAsync.rejected - :', action.payload);
        state.loading = false;
        state.isAuthenticated = false;
        state.user = null;
        state.error = action.payload as string;
      })
      
      // ========== LOGOUT ==========
      .addCase(logoutUserAsync.pending, (state) => {
        console.log('userSlice: logoutUserAsync.pending');
        state.loading = true;
      })
      .addCase(logoutUserAsync.fulfilled, (state) => {
        console.log('userSlice: logoutUserAsync.fulfilled -  ');
        state.loading = false;
        state.user = null;
        state.isAuthenticated = false;
        state.error = null;
      })
      .addCase(logoutUserAsync.rejected, (state, action) => {
        console.log('userSlice: logoutUserAsync.rejected - :', action.payload);
        state.loading = false;
        state.user = null;
        state.isAuthenticated = false;
        state.error = action.payload as string;
      })
      
      // ========== FETCH CURRENT USER ==========
      .addCase(fetchCurrentUserAsync.pending, (state) => {
        state.loading = true;
        state.error = null;
      })
      .addCase(fetchCurrentUserAsync.fulfilled, (state, action) => {
        console.log('userSlice: fetchCurrentUserAsync.fulfilled - :', action.payload.username);
        state.loading = false;
        state.user = action.payload;
        state.isAuthenticated = true;
      })
      .addCase(fetchCurrentUserAsync.rejected, (state, action) => {
        console.log('userSlice: fetchCurrentUserAsync.rejected - :', action.payload);
        state.loading = false;
        state.error = action.payload as string;
        state.isAuthenticated = false;
      });
  },
});

//  
export const { 
  clearUserError, 
  setUserFromStorage, 
  clearUser, 
  updateUserProfile
} = userSlice.actions;

export default userSlice.reducer;