// src/pages/OrderPage.tsx
import React, { useState, useEffect } from 'react';
import { useParams, useNavigate } from 'react-router-dom';
import { useAppSelector } from '../store/hooks';
import { Container, Alert, Spinner } from 'react-bootstrap';
import { api } from '../api';
import { DEFAULT_ISOTOPE_IMAGE } from '../services/api';

//    
interface Isotope {
  id: number;
  name: string;
  image_url: string | null;
  half_life: number;
  description?: string;
  decay_type?: string;
  application?: string;
}

interface IsotopeItem {
  id: number;
  isotope: number | Isotope;
  initial_amount: number;
  remaining_activity: number | null;
  isotope_order: number;
  isotope_details?: Isotope;
}

interface Order {
  id: number;
  status: string;
  time_elapsed: number;
  isotope_items: IsotopeItem[];
  total_activity: number | null;
  creation_date?: string;
  formation_date?: string | null;
  completion_date?: string | null;
  client?: any;
  moderator?: any;
}

const OrderPage: React.FC = () => {
  const { orderId } = useParams<{ orderId: string }>();
  const navigate = useNavigate();
  
  const { isAuthenticated } = useAppSelector((state) => state.user);
  const [order, setOrder] = useState<Order | null>(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState('');
  const [timeElapsed, setTimeElapsed] = useState(0);
  const [calculating, setCalculating] = useState(false);
  const [updating, setUpdating] = useState(false);
  
  //  
  useEffect(() => {
    if (!isAuthenticated) {
      navigate('/login');
      return;
    }
    
    if (!orderId || isNaN(Number(orderId))) {
      setError(' ID ');
      setLoading(false);
      return;
    }
    
    loadOrder();
  }, [orderId, isAuthenticated, navigate]);
  
  const loadOrder = async () => {
    try {
      setLoading(true);
      console.log(`   ${orderId}...`);
      
      const response = await api.isotopeOrders.isotopeOrdersRead(Number(orderId));
      
      console.log('  :', response.data);
      
      //   API   
      const apiOrder = response.data;
      const transformedOrder: Order = {
        id: apiOrder.id || 0,
        status: apiOrder.status || 'DRAFT',
        time_elapsed: apiOrder.time_elapsed || 0,
        total_activity: apiOrder.total_activity || null,
        isotope_items: (apiOrder.isotope_items || []).map((item: any) => ({
          id: item.id || 0,
          isotope: item.isotope || 0,
          initial_amount: item.initial_amount || 1.0,
          remaining_activity: item.remaining_activity || null,
          isotope_order: item.isotope_order || Number(orderId),
          isotope_details: item.isotope_details || (typeof item.isotope === 'object' ? item.isotope : undefined)
        }))
      };
      
      setOrder(transformedOrder);
      setTimeElapsed(transformedOrder.time_elapsed || 0);
      
    } catch (err: any) {
      console.error('   :', err);
      setError(` : ${err.response?.data?.detail || err.message}`);
    } finally {
      setLoading(false);
    }
  };
  
  const getIsotopeName = (item: IsotopeItem): string => {
    if (item.isotope_details) {
      return item.isotope_details.name;
    }
    
    if (typeof item.isotope === 'object' && item.isotope !== null) {
      return item.isotope.name;
    }
    
    return ` ${item.isotope}`;
  };
  
  const getIsotopeImage = (item: IsotopeItem): string => {
    if (item.isotope_details?.image_url) {
      return item.isotope_details.image_url;
    }
    
    if (typeof item.isotope === 'object' && item.isotope?.image_url) {
      return item.isotope.image_url;
    }
    
    return DEFAULT_ISOTOPE_IMAGE;
  };
  
  const getIsotopeHalfLife = (item: IsotopeItem): number => {
    if (item.isotope_details?.half_life) {
      return item.isotope_details.half_life;
    }
    
    if (typeof item.isotope === 'object' && item.isotope?.half_life) {
      return item.isotope.half_life;
    }
    
    return 0;
  };
  
  const handleCalculate = async (e: React.FormEvent) => {
    e.preventDefault();
    if (!orderId || !order) return;
    
    try {
      setCalculating(true);
      
      // 1.    
      await api.isotopeOrders.isotopeOrdersPartialUpdate(Number(orderId), {
        time_elapsed: timeElapsed
      } as any);
      
      // 2.     
      // TODO:    
      
      // 3.  
      await loadOrder();
      
      alert('   !');
      
    } catch (err: any) {
      console.error('  :', err);
      setError(` : ${err.response?.data?.detail || err.message}`);
    } finally {
      setCalculating(false);
    }
  };
  
  const handleUpdateAmount = async (itemId: number, amount: number) => {
    if (amount <= 0) {
      alert('    0');
      return;
    }
    
    try {
      setUpdating(true);
      await api.isotopeOrderItems.isotopeOrderItemsPartialUpdate(itemId, {
        initial_amount: amount
      } as any);
      
      //  
      await loadOrder();
      
    } catch (err: any) {
      console.error('  :', err);
      setError(` : ${err.response?.data?.detail || err.message}`);
    } finally {
      setUpdating(false);
    }
  };
  
  const handleRemoveItem = async (itemId: number) => {
    if (!window.confirm('   ?')) return;
    
    try {
      await api.isotopeOrderItems.isotopeOrderItemsDelete(itemId);
      
      //  
      await loadOrder();
      
      alert('    ');
      
    } catch (err: any) {
      console.error('  :', err);
      setError(` : ${err.response?.data?.detail || err.message}`);
    }
  };
  
  const handleDeleteOrder = async () => {
    if (!orderId || !window.confirm('    ?')) return;
    
    try {
      await api.isotopeOrders.isotopeOrdersDelete(Number(orderId));
      alert('  ');
      navigate('/isotopes');
      
    } catch (err: any) {
      console.error('   :', err);
      setError(`  : ${err.response?.data?.detail || err.message}`);
    }
  };
  
  const handleConfirmOrder = async () => {
    if (!orderId || !window.confirm(' ?     .')) return;
    
    try {
      await api.isotopeOrders.isotopeOrdersPartialUpdate(Number(orderId), {
        status: 'FORMED'
      } as any);
      
      alert('  !');
      await loadOrder();
      
    } catch (err: any) {
      console.error('  :', err);
      setError(` : ${err.response?.data?.detail || err.message}`);
    }
  };
  
  if (!isAuthenticated) {
    return (
      <Container className="mt-5">
        <Alert variant="warning">
              
        </Alert>
      </Container>
    );
  }
  
  if (loading) {
    return (
      <Container className="mt-5 text-center">
        <Spinner animation="border" variant="primary" />
        <p className="mt-3"> ...</p>
      </Container>
    );
  }
  
  if (error) {
    return (
      <Container className="mt-5">
        <Alert variant="danger">{error}</Alert>
      </Container>
    );
  }
  
  if (!order) {
    return (
      <Container className="mt-5">
        <Alert variant="info">  </Alert>
      </Container>
    );
  }
  
  const isDraft = order.status === 'DRAFT';
  const items = order.isotope_items || [];
  const itemsCount = items.length;
  
  return (
    <div className="order-page" style={{ maxWidth: '1200px', margin: '0 auto', padding: '20px' }}>
      <h1 style={{ 
        textAlign: 'center', 
        color: '#0B2149', 
        fontSize: '28px', 
        marginBottom: '30px',
        fontWeight: '600',
        textTransform: 'uppercase'
      }}>
           
      </h1>
      
      {isDraft && (
        <div style={{ textAlign: 'right', marginBottom: '20px' }}>
          <button
            type="button"
            onClick={handleDeleteOrder}
            style={{
              background: '#ff4444',
              color: 'white',
              border: 'none',
              padding: '10px 20px',
              borderRadius: '5px',
              cursor: 'pointer'
            }}
          >
                
          </button>
        </div>
      )}
      
      {/*   */}
      <div className="order-configuration" style={{ 
        background: 'white', 
        borderRadius: '8px', 
        padding: '25px', 
        marginBottom: '25px' 
      }}>
        <h2 style={{ 
          color: '#0B2149', 
          marginBottom: '20px', 
          fontSize: '22px', 
          fontWeight: '600' 
        }}>
             
        </h2>
        
        <form method="post" className="order-form" onSubmit={handleCalculate}>
          <div className="params-grid" style={{ 
            display: 'flex', 
            gap: '20px', 
            alignItems: 'end' 
          }}>
            <div className="param-group" style={{ 
              display: 'flex', 
              flexDirection: 'column', 
              flexGrow: 1 
            }}>
              <label htmlFor="time_elapsed" style={{ 
                marginBottom: '8px', 
                fontWeight: '600', 
                color: '#0B2149', 
                fontSize: '16px' 
              }}>
                ,    ():
              </label>
              <input
                type="number"
                id="time_elapsed"
                name="time_elapsed"
                value={timeElapsed}
                onChange={(e) => setTimeElapsed(Number(e.target.value))}
                step="any"
                min="0"
                required
                disabled={!isDraft || calculating}
                style={{
                  padding: '10px 15px',
                  border: '1px solid #ddd',
                  borderRadius: '5px',
                  fontSize: '16px'
                }}
              />
            </div>
            
            <div className="param-group">
              <button
                type="submit"
                className="calculate-btn"
                disabled={!isDraft || calculating || updating}
                style={{
                  padding: '10px 25px',
                  backgroundColor: calculating ? '#999' : '#14BF96',
                  color: 'white',
                  border: 'none',
                  borderRadius: '5px',
                  cursor: (isDraft && !calculating && !updating) ? 'pointer' : 'not-allowed',
                  fontSize: '16px',
                  height: 'fit-content'
                }}
              >
                {calculating ? '...' : '  '}
              </button>
            </div>
          </div>
        </form>
      </div>
      
      {/*   */}
      <div className="order-items-list">
        <h2 style={{ 
          color: '#0B2149', 
          marginBottom: '20px', 
          fontSize: '22px', 
          fontWeight: '600' 
        }}>
               ({itemsCount})
        </h2>
        
        <div className="items-container">
          {items.length > 0 ? (
            items.map((item) => {
              const isotopeName = getIsotopeName(item);
              const isotopeImage = getIsotopeImage(item);
              const isotopeHalfLife = getIsotopeHalfLife(item);
              
              return (
                <div 
                  key={item.id} 
                  className="order-item-horizontal" 
                  style={{
                    display: 'grid',
                    gridTemplateColumns: '2fr 1.5fr 1.5fr',
                    gap: '20px',
                    alignItems: 'center',
                    padding: '15px',
                    background: '#f8f9fa',
                    borderRadius: '6px',
                    border: '1px solid #e0e0e0',
                    marginBottom: '15px'
                  }}
                >
                  {/*     */}
                  <div className="item-main-info" style={{ display: 'flex', alignItems: 'center', gap: '15px' }}>
                    <img 
                      src={isotopeImage}
                      alt={isotopeName}
                      className="isotope-image-small"
                      style={{
                        width: '60px',
                        height: '60px',
                        objectFit: 'cover',
                        borderRadius: '5px',
                        border: '1px solid #ddd'
                      }}
                      onError={(e) => {
                        (e.target as HTMLImageElement).src = DEFAULT_ISOTOPE_IMAGE;
                      }}
                    />
                    <div className="item-details">
                      <p className="item-name" style={{ 
                        color: '#0B2149', 
                        marginBottom: '5px', 
                        fontSize: '18px', 
                        fontWeight: '600' 
                      }}>
                        {isotopeName}
                      </p>
                      <p className="item-half-life" style={{ 
                        color: '#14BF96', 
                        fontWeight: '600', 
                        fontSize: '14px' 
                      }}>
                         : {isotopeHalfLife} 
                      </p>
                    </div>
                  </div>
                  
                  {/*    */}
                  <div className="item-params" style={{ 
                    display: 'flex', 
                    flexDirection: 'column',
                    gap: '10px'
                  }}>
                    {isDraft && (
                      <button 
                        type="button"
                        onClick={() => handleRemoveItem(item.id)}
                        className="delete-item-btn" 
                        title=" "
                        style={{
                          background: 'transparent',
                          border: 'none',
                          color: '#ff4444',
                          cursor: 'pointer',
                          fontSize: '16px',
                          alignSelf: 'flex-start',
                          marginLeft: 'auto'
                        }}
                      >
                        <i className="fas fa-trash-alt"></i>
                      </button>
                    )}
                    
                    <div className="param-input-group" style={{ display: 'flex', flexDirection: 'column' }}>
                      <label htmlFor={`initial_amount_${item.id}`} style={{ 
                        fontWeight: '600', 
                        color: '#0B2149', 
                        fontSize: '14px', 
                        marginBottom: '5px' 
                      }}>
                          ():
                      </label>
                      <input
                        type="number"
                        id={`initial_amount_${item.id}`}
                        name="initial_amount"
                        value={item.initial_amount || 1.0}
                        onChange={(e) => handleUpdateAmount(item.id, Number(e.target.value))}
                        step="any"
                        min="0.01"
                        required
                        disabled={!isDraft || updating}
                        style={{
                          width: '100px',
                          padding: '8px 12px',
                          border: '1px solid #ddd',
                          borderRadius: '4px',
                          fontSize: '14px'
                        }}
                      />
                    </div>
                  </div>
                  
                  {/*  */}
                  <div className="item-results" style={{ display: 'flex', flexDirection: 'column', gap: '10px' }}>
                    <div className="result-group" style={{ display: 'flex', flexDirection: 'column' }}>
                      <span className="result-label" style={{ 
                        fontWeight: '600', 
                        color: '#333', 
                        fontSize: '14px', 
                        marginBottom: '5px' 
                      }}>
                         :
                      </span>
                      <span className="result-value" style={{ 
                        fontWeight: '700', 
                        color: '#14BF96', 
                        fontSize: '16px' 
                      }}>
                        {item.remaining_activity ? 
                          `${item.remaining_activity.toFixed(2)} ` : 
                          '-'
                        }
                      </span>
                    </div>
                  </div>
                </div>
              );
            })
          ) : (
            <div style={{ textAlign: 'center', padding: '40px' }}>
              <p>        </p>
              <button
                type="button"
                onClick={() => navigate('/isotopes')}
                style={{ 
                  color: '#14BF96',
                  background: 'none',
                  border: 'none',
                  cursor: 'pointer',
                  fontSize: '16px',
                  textDecoration: 'underline',
                  marginTop: '10px'
                }}
              >
                 
              </button>
            </div>
          )}
        </div>

        {items.length > 0 && (
          <div className="total-result" style={{ 
            borderTop: '2px solid #e0e0e0', 
            paddingTop: '20px', 
            marginTop: '20px' 
          }}>
            <div className="total-item" style={{ 
              display: 'flex', 
              justifyContent: 'space-between', 
              alignItems: 'center', 
              padding: '15px 20px', 
              background: '#0B2149', 
              borderRadius: '6px', 
              color: 'white' 
            }}>
              <span className="total-label" style={{ fontWeight: '600', fontSize: '18px' }}>
                   :
              </span>
              <span className="total-value" style={{ fontWeight: '700', fontSize: '20px', color: '#14BF96' }}>
                {order.total_activity ? 
                  `${order.total_activity.toFixed(2)} ` : 
                  '-'
                }
              </span>
            </div>
          </div>
        )}
      </div>

      {/*    (  ) */}
      {isDraft && items.length > 0 && (
        <div style={{ textAlign: 'center', marginTop: '30px' }}>
          <button
            type="button"
            onClick={handleConfirmOrder}
            style={{
              padding: '12px 30px',
              backgroundColor: '#0B2149',
              color: 'white',
              border: 'none',
              borderRadius: '5px',
              cursor: 'pointer',
              fontSize: '16px'
            }}
          >
            <i className="fas fa-check me-2"></i>
             
          </button>
        </div>
      )}
    </div>
  );
};

export default OrderPage;