import React, { useState, useEffect } from 'react';
import { Link, useLocation, useNavigate } from 'react-router-dom';
import { useAppSelector, useAppDispatch } from '../store/hooks';
import { logoutUserAsync } from '../features/user/userSlice';
import { clearFilters } from '../features/filters/filtersSlice';
import { clearDraftOrder } from '../features/orders/ordersSlice';
import { Container, Nav, Navbar, Button, Dropdown } from 'react-bootstrap';

const SimpleNavbar: React.FC = () => {
  const location = useLocation();
  const navigate = useNavigate();
  const [isMenuOpen, setIsMenuOpen] = useState(false);
  
  const dispatch = useAppDispatch();
  const { isAuthenticated, user, loading } = useAppSelector((state) => state.user);

  //   
  console.log('SimpleNavbar:  ', { 
    isAuthenticated, 
    user, 
    loading,
    hasUser: !!user,
    localStorageUser: localStorage.getItem('user')
  });

  //     
  useEffect(() => {
    setIsMenuOpen(false);
  }, [location]);

  const toggleMenu = () => {
    setIsMenuOpen(!isMenuOpen);
  };

  const handleLogout = async () => {
    console.log('handleLogout:  ');
    console.log('handleLogout:    :', user);
    console.log('handleLogout: localStorage  :', {
      user: localStorage.getItem('user'),
      token: localStorage.getItem('token')
    });
    
    try {
      console.log('handleLogout:  logoutUserAsync');
      const result = await dispatch(logoutUserAsync());
      console.log('handleLogout:  logoutUserAsync:', result);
      
      // ,    logout
      if (logoutUserAsync.fulfilled.match(result)) {
        console.log('handleLogout: logoutUserAsync  ');
        dispatch(clearFilters());
        dispatch(clearDraftOrder());
        
        console.log('handleLogout: localStorage  :', {
          user: localStorage.getItem('user'),
          token: localStorage.getItem('token')
        });
        
        console.log('handleLogout:   ');
        navigate('/');
      } else {
        console.error('handleLogout: logoutUserAsync :', result);
        alert('  .   .');
      }
    } catch (error) {
      console.error('handleLogout:   :', error);
      alert('     ');
    }
  };

  //     
  const isActive = (path: string) => {
    return location.pathname === path;
  };

  //    Link    react-bootstrap
  const CustomLink = React.forwardRef<
    HTMLAnchorElement,
    { to: string; children: React.ReactNode; className?: string; style?: React.CSSProperties }
  >(({ to, children, className, style, ...props }, ref) => (
    <Link 
      to={to} 
      ref={ref} 
      className={className} 
      style={style}
      {...props}
    >
      {children}
    </Link>
  ));

  //   Dropdown.Item  Link
  const CustomDropdownItem = React.forwardRef<
    HTMLAnchorElement,
    { to: string; children: React.ReactNode }
  >(({ to, children, ...props }, ref) => (
    <Dropdown.Item 
      as={Link as any} 
      to={to} 
      ref={ref} 
      {...props}
    >
      {children}
    </Dropdown.Item>
  ));

  return (
    <>
      {/*      */}
      <div className="header">
        <h1>    </h1>
        <Link to="/" className="home-link">
          <i className="fas fa-home"></i>
        </Link>
      </div>

      {/*     React Bootstrap */}
      <Navbar expand="lg" className="custom-navbar" style={{ backgroundColor: '#f8f9fa' }}>
        <Container>
          <Navbar.Toggle 
            aria-controls="basic-navbar-nav" 
            onClick={toggleMenu}
            style={{ borderColor: '#0B2149' }}
          >
            <i className="fas fa-bars" style={{ color: '#0B2149' }}></i>
          </Navbar.Toggle>
          
          <Navbar.Collapse id="basic-navbar-nav" in={isMenuOpen}>
            <Nav className="me-auto">
              <Nav.Link 
                as={CustomLink as any} 
                to="/"
                style={{ 
                  color: isActive('/') ? '#14BF96' : '#0B2149',
                  fontWeight: isActive('/') ? 'bold' : 'normal'
                }}
              >
                <i className="fas fa-home me-1"></i> 
              </Nav.Link>
              
              <Nav.Link 
                as={CustomLink as any} 
                to="/isotopes"
                style={{ 
                  color: isActive('/isotopes') ? '#14BF96' : '#0B2149',
                  fontWeight: isActive('/isotopes') ? 'bold' : 'normal'
                }}
              >
                <i className="fas fa-vial me-1"></i> 
              </Nav.Link>
              
              {/*      */}
              {isAuthenticated && (
                <>
                  <Nav.Link 
                    as={CustomLink as any} 
                    to="/orders"
                    style={{ 
                      color: isActive('/orders') ? '#14BF96' : '#0B2149',
                      fontWeight: isActive('/orders') ? 'bold' : 'normal'
                    }}
                  >
                    <i className="fas fa-list me-1"></i>  
                  </Nav.Link>
                  
                  <Nav.Link 
                    as={CustomLink as any} 
                    to="/profile"
                    style={{ 
                      color: isActive('/profile') ? '#14BF96' : '#0B2149',
                      fontWeight: isActive('/profile') ? 'bold' : 'normal'
                    }}
                  >
                    <i className="fas fa-user me-1"></i>  
                  </Nav.Link>
                </>
              )}
            </Nav>
            
            {/*    -  */}
            <Nav>
              {isAuthenticated ? (
                <Dropdown align="end">
                  <Dropdown.Toggle 
                    variant="link" 
                    id="dropdown-user"
                    style={{ 
                      color: '#0B2149',
                      textDecoration: 'none'
                    }}
                  >
                    <i className="fas fa-user-circle me-1"></i>
                    <span style={{ fontWeight: 'bold' }}>
                      {user?.username || user?.email || ''}
                    </span>
                    {loading && (
                      <span style={{ marginLeft: '5px', fontSize: '12px', color: '#666' }}>
                        (...)
                      </span>
                    )}
                  </Dropdown.Toggle>
                  
                  <Dropdown.Menu>
                    <CustomDropdownItem to="/profile">
                      <i className="fas fa-user me-2"></i>  
                    </CustomDropdownItem>
                    <CustomDropdownItem to="/orders">
                      <i className="fas fa-list me-2"></i>  
                    </CustomDropdownItem>
                    <Dropdown.Divider />
                    <Dropdown.Item 
                      onClick={handleLogout}
                      disabled={loading}
                    >
                      {loading ? (
                        <>
                          <i className="fas fa-spinner fa-spin me-2"></i> ...
                        </>
                      ) : (
                        <>
                          <i className="fas fa-sign-out-alt me-2"></i> 
                        </>
                      )}
                    </Dropdown.Item>
                  </Dropdown.Menu>
                </Dropdown>
              ) : (
                <>
                  <Nav.Link 
                    as={CustomLink as any} 
                    to="/login"
                    className="me-2"
                    style={{ 
                      color: isActive('/login') ? '#14BF96' : '#0B2149',
                      fontWeight: isActive('/login') ? 'bold' : 'normal'
                    }}
                  >
                    <i className="fas fa-sign-in-alt me-1"></i> 
                  </Nav.Link>
                  
                  <Button 
                    as={CustomLink as any}
                    to="/register"
                    variant="outline-primary"
                    style={{ 
                      borderColor: '#0B2149',
                      color: '#0B2149'
                    }}
                    className="register-btn"
                  >
                    <i className="fas fa-user-plus me-1"></i> 
                  </Button>
                </>
              )}
            </Nav>
          </Navbar.Collapse>
        </Container>
      </Navbar>
    </>
  );
};

export default SimpleNavbar;