// frontend/target_config.ts
const YOUR_HOST_IP = '10.92.32.164';

const isGitHubPages = window.location.hostname.includes('github.io');
const isLocalhost = window.location.hostname.includes('localhost') || 
                    window.location.hostname.includes('127.0.0.1');

const currentProtocol = window.location.protocol;

let api_proxy_addr: string;
let img_proxy_addr: string;
let dest_api: string;
let dest_img: string;

if (isGitHubPages) {
  // - URL
  api_proxy_addr = `//${YOUR_HOST_IP}:8000`;
  img_proxy_addr = `//${YOUR_HOST_IP}:9000`;
  dest_api = api_proxy_addr;
  dest_img = img_proxy_addr;
} else if (isLocalhost) {
  api_proxy_addr = "http://localhost:8000";
  img_proxy_addr = "http://localhost:9000";
  dest_api = "/api";
  dest_img = "/minio";
} else {
  api_proxy_addr = "http://localhost:8000";
  img_proxy_addr = "http://localhost:9000";
  dest_api = "/api";
  dest_img = "/minio";
}

const dest_root = "";

export {
  isGitHubPages as target_github_pages,
  api_proxy_addr,
  img_proxy_addr,
  dest_api,
  dest_img,
  dest_root
};

export const CONFIG = {
  isGitHubPages,
  isLocalhost,
  apiBaseUrl: dest_api,
  imgBaseUrl: dest_img,
  hostIP: YOUR_HOST_IP,
  currentProtocol
};