import React, { useState } from 'react';
import { Container, Card, Form, Button, Alert, Spinner } from 'react-bootstrap';
import { useAppSelector } from '../store/hooks';

const Profile: React.FC = () => {
  const { user, loading } = useAppSelector((state) => state.user);
  const [currentPassword, setCurrentPassword] = useState('');
  const [newPassword, setNewPassword] = useState('');
  const [confirmPassword, setConfirmPassword] = useState('');
  const [showPassword, setShowPassword] = useState(false);
  const [message, setMessage] = useState<{ type: 'success' | 'danger', text: string } | null>(null);

  const handlePasswordChange = async (e: React.FormEvent) => {
    e.preventDefault();
    
    if (newPassword !== confirmPassword) {
      setMessage({ type: 'danger', text: '     ' });
      return;
    }
    
    if (newPassword.length < 6) {
      setMessage({ type: 'danger', text: '      6 ' });
      return;
    }
    
    //    API   
    setMessage({ type: 'success', text: '   ()' });
    setCurrentPassword('');
    setNewPassword('');
    setConfirmPassword('');
  };

  if (!user) {
    return (
      <Container className="mt-5">
        <Alert variant="warning">
          <i className="fas fa-exclamation-triangle me-2"></i>
          ,        
        </Alert>
      </Container>
    );
  }

  return (
    <Container className="mt-5">
      <h2 className="mb-4" style={{ color: '#0B2149' }}>
        <i className="fas fa-user me-2"></i>  
      </h2>
      
      <div className="row">
        <div className="col-md-6 mb-4">
          <Card className="shadow-sm">
            <Card.Body>
              <Card.Title style={{ color: '#0B2149' }}>
                <i className="fas fa-id-card me-2"></i>   
              </Card.Title>
              
              <div className="mb-3">
                <p><strong> :</strong> {user.username}</p>
                <p><strong>Email:</strong> {user.email}</p>
                <p><strong>:</strong> {user.first_name || ' '}</p>
                <p><strong>:</strong> {user.last_name || ' '}</p>
              </div>
              
              <Button 
                variant="outline-primary" 
                className="w-100"
                onClick={() => alert('    ')}
              >
                <i className="fas fa-edit me-2"></i>  
              </Button>
            </Card.Body>
          </Card>
        </div>
        
        <div className="col-md-6">
          <Card className="shadow-sm">
            <Card.Body>
              <Card.Title style={{ color: '#0B2149' }}>
                <i className="fas fa-key me-2"></i>  
              </Card.Title>
              
              {message && (
                <Alert variant={message.type} className="mb-3">
                  <i className={`fas fa-${message.type === 'success' ? 'check' : 'exclamation'}-circle me-2`}></i>
                  {message.text}
                </Alert>
              )}
              
              <Form onSubmit={handlePasswordChange}>
                <Form.Group className="mb-3">
                  <Form.Label> </Form.Label>
                  <div className="position-relative">
                    <Form.Control
                      type={showPassword ? 'text' : 'password'}
                      value={currentPassword}
                      onChange={(e) => setCurrentPassword(e.target.value)}
                      placeholder="  "
                    />
                  </div>
                </Form.Group>
                
                <Form.Group className="mb-3">
                  <Form.Label> </Form.Label>
                  <div className="position-relative">
                    <Form.Control
                      type={showPassword ? 'text' : 'password'}
                      value={newPassword}
                      onChange={(e) => setNewPassword(e.target.value)}
                      placeholder="  "
                    />
                  </div>
                </Form.Group>
                
                <Form.Group className="mb-4">
                  <Form.Label>  </Form.Label>
                  <div className="position-relative">
                    <Form.Control
                      type={showPassword ? 'text' : 'password'}
                      value={confirmPassword}
                      onChange={(e) => setConfirmPassword(e.target.value)}
                      placeholder="  "
                    />
                    <Button
                      variant="link"
                      className="position-absolute top-50 end-0 translate-middle-y me-2"
                      onClick={() => setShowPassword(!showPassword)}
                      style={{ color: '#666' }}
                    >
                      {showPassword ? (
                        <i className="fas fa-eye-slash"></i>
                      ) : (
                        <i className="fas fa-eye"></i>
                      )}
                    </Button>
                  </div>
                </Form.Group>
                
                <Button
                  type="submit"
                  disabled={loading}
                  className="w-100"
                  style={{ 
                    backgroundColor: '#0B2149', 
                    borderColor: '#0B2149'
                  }}
                >
                  {loading ? (
                    <>
                      <Spinner
                        as="span"
                        animation="border"
                        size="sm"
                        role="status"
                        aria-hidden="true"
                        className="me-2"
                      />
                      ...
                    </>
                  ) : (
                    <>
                      <i className="fas fa-save me-2"></i>
                       
                    </>
                  )}
                </Button>
              </Form>
            </Card.Body>
          </Card>
        </div>
      </div>
    </Container>
  );
};

export default Profile;