import React, { useState, useEffect } from 'react';
import { useNavigate, Link } from 'react-router-dom';
import { useAppDispatch, useAppSelector } from '../store/hooks';
import { loginUserAsync } from '../features/user/userSlice';
import { Container, Form, Button, Alert, Spinner, Card } from 'react-bootstrap';

const Login: React.FC = () => {
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const [showPassword, setShowPassword] = useState(false);
  
  const dispatch = useAppDispatch();
  const navigate = useNavigate();
  
  const { user, isAuthenticated, loading, error } = useAppSelector(
    (state) => state.user
  );

  //    ,   
  useEffect(() => {
    if (isAuthenticated) {
      navigate('/');
    }
  }, [isAuthenticated, navigate]);

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    if (!email || !password) {
      alert(',   ');
      return;
    }
    
    const result = await dispatch(loginUserAsync({ email, password }));
    
    if (loginUserAsync.fulfilled.match(result)) {
      alert(' !');
      navigate('/');
    }
  };

  return (
    <Container className="mt-5" style={{ maxWidth: '500px' }}>
      <Card className="shadow-sm">
        <Card.Body>
          <h2 className="text-center mb-4" style={{ color: '#0B2149' }}>
              
          </h2>
          
          {error && (
            <Alert variant="danger" className="mb-3">
              <i className="fas fa-exclamation-circle me-2"></i>
              {error}
            </Alert>
          )}
          
          <Form onSubmit={handleSubmit}>
            <Form.Group className="mb-3">
              <Form.Label style={{ color: '#0B2149' }}>
                Email:
              </Form.Label>
              <Form.Control
                type="email"
                value={email}
                onChange={(e) => setEmail(e.target.value)}
                required
                placeholder="  email"
              />
            </Form.Group>
            
            <Form.Group className="mb-4">
              <Form.Label style={{ color: '#0B2149' }}>
                :
              </Form.Label>
              <div className="position-relative">
                <Form.Control
                  type={showPassword ? 'text' : 'password'}
                  value={password}
                  onChange={(e) => setPassword(e.target.value)}
                  required
                  placeholder="  "
                />
                <Button
                  variant="link"
                  className="position-absolute top-50 end-0 translate-middle-y me-2"
                  onClick={() => setShowPassword(!showPassword)}
                  style={{ color: '#666' }}
                >
                  {showPassword ? (
                    <i className="fas fa-eye-slash"></i>
                  ) : (
                    <i className="fas fa-eye"></i>
                  )}
                </Button>
              </div>
            </Form.Group>
            
            <Button
              type="submit"
              disabled={loading}
              className="w-100 mb-3"
              style={{ 
                backgroundColor: '#14BF96', 
                borderColor: '#14BF96',
                height: '45px'
              }}
            >
              {loading ? (
                <>
                  <Spinner
                    as="span"
                    animation="border"
                    size="sm"
                    role="status"
                    aria-hidden="true"
                    className="me-2"
                  />
                  ...
                </>
              ) : (
                <>
                  <i className="fas fa-sign-in-alt me-2"></i>
                  
                </>
              )}
            </Button>
          </Form>
          
          <div className="text-center mt-3">
            <p style={{ color: '#666' }}>
               ?{' '}
              <Link 
                to="/register" 
                style={{ 
                  color: '#14BF96', 
                  textDecoration: 'none',
                  fontWeight: 'bold'
                }}
              >
                
              </Link>
            </p>
          </div>
        </Card.Body>
      </Card>
    </Container>
  );
};

export default Login;