// App.tsx
import { BrowserRouter as Router, Routes, Route, Navigate } from 'react-router-dom';
import { useEffect } from 'react';
import { useAppDispatch } from './store/hooks';
import { setUserFromStorage } from './features/user/userSlice';
import SimpleNavbar from './components/SimpleNavbar';
import Breadcrumbs from './components/Breadcrumbs';
import Home from './pages/Home';
import IsotopesList from './pages/IsotopesList';
import IsotopeDetail from './pages/IsotopeDetail';
import Login from './pages/Login';
import Register from './pages/Register';
import Profile from './pages/Profile';
import OrdersList from './pages/OrdersList';
import 'bootstrap/dist/css/bootstrap.min.css';
import './styles/backend-styles.css';
import './styles/App.css';
import './styles/adaptive.css';
import OrderPage from './pages/OrderPage';

function App() {
  const dispatch = useAppDispatch();

  useEffect(() => {
    dispatch(setUserFromStorage());
  }, [dispatch]);

  //  basename   URL
  const getBasename = () => {
      return '/isotope-calc6/';
  };

  return (
    <Router basename={getBasename()}>
      <div className="App">
        <SimpleNavbar />
        <Breadcrumbs />
        <main>
          <Routes>
            <Route path="/orders/:orderId" element={<OrderPage />} />
            <Route path="/" element={<Home />} />
            <Route path="/isotopes" element={<IsotopesList />} />
            <Route path="/isotopes/:id" element={<IsotopeDetail />} />
            <Route path="/login" element={<Login />} />
            <Route path="/register" element={<Register />} />
            <Route path="/profile" element={<Profile />} />
            <Route path="/orders" element={<OrdersList />} />
            <Route path="*" element={<Navigate to="/" replace />} />
          </Routes>
        </main>
      </div>
    </Router>
  );
}

export default App;