import React, { useState, useEffect } from 'react';
import { useNavigate, Link } from 'react-router-dom';
import { useAppDispatch, useAppSelector } from '../store/hooks';
import { registerUserAsync } from '../features/user/userSlice';
import { Container, Form, Button, Alert, Spinner, Card } from 'react-bootstrap';

const Register: React.FC = () => {
  const [username, setUsername] = useState('');
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const [confirmPassword, setConfirmPassword] = useState('');
  const [showPassword, setShowPassword] = useState(false);
  const [showConfirmPassword, setShowConfirmPassword] = useState(false);
  const [errors, setErrors] = useState<{
    username?: string;
    email?: string;
    password?: string;
    confirmPassword?: string;
  }>({});
  
  const dispatch = useAppDispatch();
  const navigate = useNavigate();
  
  const { user, isAuthenticated, loading, error } = useAppSelector(
    (state) => state.user
  );

  //    ,   
  useEffect(() => {
    if (isAuthenticated) {
      navigate('/');
    }
  }, [isAuthenticated, navigate]);

  const validateForm = () => {
    const newErrors: typeof errors = {};
    
    if (!username.trim()) {
      newErrors.username = '  ';
    } else if (username.length < 3) {
      newErrors.username = '      3 ';
    }
    
    if (!email.trim()) {
      newErrors.email = 'Email ';
    } else if (!/\S+@\S+\.\S+/.test(email)) {
      newErrors.email = '  email';
    }
    
    if (!password) {
      newErrors.password = ' ';
    } else if (password.length < 6) {
      newErrors.password = '     6 ';
    }
    
    if (password !== confirmPassword) {
      newErrors.confirmPassword = '  ';
    }
    
    setErrors(newErrors);
    return Object.keys(newErrors).length === 0;
  };

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    
    if (!validateForm()) {
      return;
    }
    
    const result = await dispatch(
      registerUserAsync({ username, email, password })
    );
    
    if (registerUserAsync.fulfilled.match(result)) {
      alert(' !    .');
      navigate('/login');
    }
  };

  return (
    <Container className="mt-5" style={{ maxWidth: '500px' }}>
      <Card className="shadow-sm">
        <Card.Body>
          <h2 className="text-center mb-4" style={{ color: '#0B2149' }}>
            
          </h2>
          
          {error && (
            <Alert variant="danger" className="mb-3">
              <i className="fas fa-exclamation-circle me-2"></i>
              {error}
            </Alert>
          )}
          
          <Form onSubmit={handleSubmit}>
            <Form.Group className="mb-3">
              <Form.Label style={{ color: '#0B2149' }}>
                 :
              </Form.Label>
              <Form.Control
                type="text"
                value={username}
                onChange={(e) => setUsername(e.target.value)}
                required
                isInvalid={!!errors.username}
                placeholder="  "
              />
              {errors.username && (
                <Form.Control.Feedback type="invalid">
                  {errors.username}
                </Form.Control.Feedback>
              )}
            </Form.Group>
            
            <Form.Group className="mb-3">
              <Form.Label style={{ color: '#0B2149' }}>
                Email:
              </Form.Label>
              <Form.Control
                type="email"
                value={email}
                onChange={(e) => setEmail(e.target.value)}
                required
                isInvalid={!!errors.email}
                placeholder="  email"
              />
              {errors.email && (
                <Form.Control.Feedback type="invalid">
                  {errors.email}
                </Form.Control.Feedback>
              )}
            </Form.Group>
            
            <Form.Group className="mb-3">
              <Form.Label style={{ color: '#0B2149' }}>
                :
              </Form.Label>
              <div className="position-relative">
                <Form.Control
                  type={showPassword ? 'text' : 'password'}
                  value={password}
                  onChange={(e) => setPassword(e.target.value)}
                  required
                  isInvalid={!!errors.password}
                  placeholder="  (. 6 )"
                />
                <Button
                  variant="link"
                  className="position-absolute top-50 end-0 translate-middle-y me-2"
                  onClick={() => setShowPassword(!showPassword)}
                  style={{ color: '#666' }}
                >
                  {showPassword ? (
                    <i className="fas fa-eye-slash"></i>
                  ) : (
                    <i className="fas fa-eye"></i>
                  )}
                </Button>
                {errors.password && (
                  <Form.Control.Feedback type="invalid">
                    {errors.password}
                  </Form.Control.Feedback>
                )}
              </div>
            </Form.Group>
            
            <Form.Group className="mb-4">
              <Form.Label style={{ color: '#0B2149' }}>
                 :
              </Form.Label>
              <div className="position-relative">
                <Form.Control
                  type={showConfirmPassword ? 'text' : 'password'}
                  value={confirmPassword}
                  onChange={(e) => setConfirmPassword(e.target.value)}
                  required
                  isInvalid={!!errors.confirmPassword}
                  placeholder=" "
                />
                <Button
                  variant="link"
                  className="position-absolute top-50 end-0 translate-middle-y me-2"
                  onClick={() => setShowConfirmPassword(!showConfirmPassword)}
                  style={{ color: '#666' }}
                >
                  {showConfirmPassword ? (
                    <i className="fas fa-eye-slash"></i>
                  ) : (
                    <i className="fas fa-eye"></i>
                  )}
                </Button>
                {errors.confirmPassword && (
                  <Form.Control.Feedback type="invalid">
                    {errors.confirmPassword}
                  </Form.Control.Feedback>
                )}
              </div>
            </Form.Group>
            
            <Button
              type="submit"
              disabled={loading}
              className="w-100 mb-3"
              style={{ 
                backgroundColor: '#0B2149', 
                borderColor: '#0B2149',
                height: '45px'
              }}
            >
              {loading ? (
                <>
                  <Spinner
                    as="span"
                    animation="border"
                    size="sm"
                    role="status"
                    aria-hidden="true"
                    className="me-2"
                  />
                  ...
                </>
              ) : (
                <>
                  <i className="fas fa-user-plus me-2"></i>
                  
                </>
              )}
            </Button>
          </Form>
          
          <div className="text-center mt-3">
            <p style={{ color: '#666' }}>
                ?{' '}
              <Link 
                to="/login" 
                style={{ 
                  color: '#14BF96', 
                  textDecoration: 'none',
                  fontWeight: 'bold'
                }}
              >
                
              </Link>
            </p>
          </div>
        </Card.Body>
      </Card>
    </Container>
  );
};

export default Register;