// src/pages/IsotopesList.tsx
import React, { useState, useEffect, useRef } from 'react';
import { Link, useNavigate } from 'react-router-dom';
import { isotopeService, cartService, DEFAULT_ISOTOPE_IMAGE } from '../services/api';
import type { Isotope } from '../services/api';
import { useAppDispatch, useAppSelector } from '../store/hooks';
import { setNameFilter } from '../features/filters/filtersSlice';
import { CONFIG } from '../../target_config';

const IsotopesList: React.FC = () => {
  const [isotopes, setIsotopes] = useState<Isotope[]>([]);
  const [loading, setLoading] = useState<boolean>(true);
  const [error, setError] = useState<string>('');
  const [buttonColor, setButtonColor] = useState<string>('#14BF96');
  const [searchQuery, setSearchQuery] = useState<string>('');
  const [cartCount, setCartCount] = useState<number>(0);
  const [addingToCart, setAddingToCart] = useState<boolean>(false);
  const [backendStatus, setBackendStatus] = useState<'checking' | 'connected' | 'disconnected'>('checking');
  const navigate = useNavigate();

  const filters = useAppSelector((state) => state.filters);
  const dispatch = useAppDispatch();

  const isMounted = useRef(false);
  const updateCount = useRef(0);
  const prevFilterRef = useRef<string>('');

  const isCartEmpty = cartCount === 0;

  useEffect(() => {
    loadIsotopes();
    checkBackendConnection();
    
    //    
    const loadInitialCartCount = async () => {
      try {
        console.log('    ...');
        const count = await cartService.getCartCount();
        setCartCount(count);
        console.log('   :', count);
      } catch (error) {
        console.warn('    :', error);
        //  GitHub Pages  demo 
        if (CONFIG.isGitHubPages) {
          setCartCount(3); // Demo 
        }
      }
    };
    
    loadInitialCartCount();
  }, [filters.name]);

  useEffect(() => {
    if (!isMounted.current) {
      isMounted.current = true;
      setButtonColor('#14BF96');
    } else if (filters.name !== prevFilterRef.current) {
      prevFilterRef.current = filters.name;
      updateCount.current += 1;
      const newColor = updateCount.current % 2 === 1 ? '#0B2149' : '#14BF96';
      setButtonColor(newColor);
    }
  }, [filters.name]);

  const checkBackendConnection = async () => {
    try {
      setBackendStatus('checking');
      const connection = await isotopeService.checkConnection();
      setBackendStatus(connection.connected ? 'connected' : 'disconnected');
      console.log('  :', connection);
    } catch (error) {
      setBackendStatus('disconnected');
      console.log('    :', error);
    }
  };

  const loadIsotopes = async () => {
    try {
      setLoading(true);
      setError('');
      
      console.log('    :', filters);
      console.log(' :', CONFIG.isGitHubPages ? 'GitHub Pages' : 'Local');
      
      const filtersToSend = {
        name: filters.name,
      };
      
      const data = await isotopeService.getIsotopes(filtersToSend);
      console.log('  :', data.length, '');
      
      setIsotopes(data);
      
      if (data.length === 0) {
        setError('  ');
      }
    } catch (err: any) {
      console.error('    :', err);
      setError('    : ' + err.message);
      setIsotopes([]);
    } finally {
      setLoading(false);
    }
  };

  const handleSearch = (e: React.FormEvent) => {
    e.preventDefault();
    dispatch(setNameFilter(searchQuery));
  };

  const handleInputChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const newValue = e.target.value;
    setSearchQuery(newValue);
    if (newValue === '') {
      dispatch(setNameFilter(''));
    }
  };

  const handleAddToCart = async (isotopeId: number, isotopeName: string) => {
    if (addingToCart) return;
    
    //   
    if (!cartService.isCartAvailable()) {
      alert(`-:  "${isotopeName}"    ()`);
      setCartCount(prev => prev + 1); //    
      return;
    }
    
    try {
      setAddingToCart(true);
      console.log('   ...', isotopeId);
      
      //   
      await cartService.addToCart(isotopeId);
      
      //  
      const newCount = await cartService.getCartCount();
      setCartCount(newCount);
      
      alert(`  ${isotopeName}   ! (: ${newCount})`);
      
    } catch (error: any) {
      console.error('  :', error);
      
      //    
      const errorMsg = error.message || ' ';
      
      alert(` : ${errorMsg}\n\n Console (F12)  .`);
    } finally {
      setAddingToCart(false);
    }
  };

  const handleCartClick = async () => {
    if (isCartEmpty) {
      alert('    ');
      return;
    }
    
    //  GitHub Pages  -
    if (CONFIG.isGitHubPages) {
      alert('-:  GitHub Pages   .      .');
      return;
    }
    
    try {
      console.log('   ...');
      
      //   
      const draft = await cartService.getDraftOrder();
      console.log('  :', draft);
      
      if (draft && draft.id) {
        console.log(`    ${draft.id}`);
        navigate(`/orders/${draft.id}`);
      } else {
        console.warn('      ID');
        alert('   .     .');
      }
      
    } catch (error: any) {
      console.error('    :', error);
      alert(`   : ${error.message}`);
    }
  };

  const getIsotopeImage = (isotope: Isotope) => {
    if (!isotope.image_url) {
      return DEFAULT_ISOTOPE_IMAGE;
    }
    return isotope.image_url;
  };

  const handleImageError = (e: React.SyntheticEvent<HTMLImageElement, Event>) => {
    const target = e.target as HTMLImageElement;
    target.src = DEFAULT_ISOTOPE_IMAGE;
  };

  //   
  const ConnectionStatus = () => {
    if (CONFIG.isGitHubPages) {
      return (
        <div style={{
          padding: '10px 20px',
          margin: '20px auto',
          maxWidth: '800px',
          backgroundColor: '#fff3cd',
          border: '1px solid #ffeeba',
          borderRadius: '8px',
          textAlign: 'center'
        }}>
          <i className="fas fa-info-circle me-2" style={{ color: '#856404' }}></i>
          <strong>- GitHub Pages</strong>
          <div style={{ marginTop: '5px', fontSize: '14px' }}>
            {backendStatus === 'checking' && '   ...'}
            {backendStatus === 'connected' && '    '}
            {backendStatus === 'disconnected' && '  -.      .'}
          </div>
          <div style={{ marginTop: '10px', fontSize: '12px', color: '#666' }}>
            API URL: {CONFIG.apiBaseUrl}
          </div>
        </div>
      );
    }
    
    return (
      <div style={{
        padding: '10px 20px',
        margin: '10px auto',
        maxWidth: '800px',
        backgroundColor: backendStatus === 'connected' ? '#d4edda' : '#f8d7da',
        border: `1px solid ${backendStatus === 'connected' ? '#c3e6cb' : '#f5c6cb'}`,
        borderRadius: '8px',
        textAlign: 'center'
      }}>
        <i className={`fas fa-${backendStatus === 'connected' ? 'check' : 'times'}-circle me-2`} 
           style={{ color: backendStatus === 'connected' ? '#155724' : '#721c24' }}></i>
        <strong>
          {backendStatus === 'checking' && ' ...'}
          {backendStatus === 'connected' && '   '}
          {backendStatus === 'disconnected' && '    '}
        </strong>
      </div>
    );
  };

  if (loading) {
    return (
      <div className="container">
        <ConnectionStatus />
        <div style={{ textAlign: 'center', padding: '40px' }}>
          <div className="spinner-border text-primary" role="status">
            <span className="visually-hidden">...</span>
          </div>
          <p style={{ marginTop: '20px' }}> ...</p>
        </div>
      </div>
    );
  }

  return (
    <div className="container">
      <ConnectionStatus />
      
      {/*  / */}
      <div className="cart-container">
        <div 
          className={`cart-icon ${isCartEmpty ? 'inactive' : 'active'}`}
          onClick={handleCartClick}
          style={{
            cursor: isCartEmpty ? 'not-allowed' : 'pointer',
            position: 'relative',
            backgroundColor: isCartEmpty ? '#f8f9fa' : '#ffffff',
            borderColor: isCartEmpty ? '#cccccc' : '#0B2149',
            color: isCartEmpty ? '#cccccc' : '#0B2149'
          }}
        >
          <i className="fas fa-calculator"></i>
          
          {/*     */}
          {!isCartEmpty && cartCount > 0 && (
            <span className="cart-count" style={{
              position: 'absolute',
              top: '5px',
              right: '5px',
              backgroundColor: '#ff4444',
              color: 'white',
              borderRadius: '50%',
              width: '20px',
              height: '20px',
              fontSize: '12px',
              display: 'flex',
              alignItems: 'center',
              justifyContent: 'center',
              fontWeight: 'bold'
            }}>
              {cartCount}
            </span>
          )}
          
          {/*  -  GitHub Pages */}
          {CONFIG.isGitHubPages && (
            <div style={{
              position: 'absolute',
              top: '-5px',
              right: '-5px',
              backgroundColor: '#ff9900',
              color: 'white',
              borderRadius: '50%',
              width: '15px',
              height: '15px',
              fontSize: '10px',
              display: 'flex',
              alignItems: 'center',
              justifyContent: 'center',
              fontWeight: 'bold'
            }}>
              D
            </div>
          )}
        </div>
      </div>

      {/*   */}
      <div className="search-form" style={{ margin: '30px auto', maxWidth: '600px', padding: '0 20px' }}>
        <form onSubmit={handleSearch} style={{ display: 'flex', gap: '15px', justifyContent: 'center', alignItems: 'center' }}>
          <input
            type="text"
            value={searchQuery}
            onChange={handleInputChange}
            placeholder="  ..."
            style={{
              padding: '12px 20px',
              border: '1px solid #ddd',
              borderRadius: '5px',
              fontSize: '16px',
              flex: '1',
              minWidth: '200px'
            }}
          />
          <button 
            type="submit"
            style={{ 
              backgroundColor: buttonColor,
              padding: '12px 30px',
              color: 'white',
              border: 'none',
              borderRadius: '5px',
              cursor: 'pointer',
              fontSize: '16px',
              whiteSpace: 'nowrap'
            }}
          >
             
          </button>
        </form>
      </div>

      {error && (
        <div className="alert alert-error" style={{
          backgroundColor: '#f8d7da',
          color: '#721c24',
          padding: '15px',
          borderRadius: '5px',
          margin: '20px auto',
          maxWidth: '800px',
          border: '1px solid #f5c6cb'
        }}>
          {error}
          {isotopes.length === 0 && (
            <p style={{ marginTop: '10px' }}>
                .    Django.
            </p>
          )}
        </div>
      )}

      <div className="services-container" style={{
        display: 'grid',
        gridTemplateColumns: 'repeat(3, 1fr)',
        gap: '30px',
        margin: '40px auto',
        maxWidth: '1200px',
        padding: '0 20px'
      }}>
        {isotopes.map(isotope => (
          <div key={isotope.id} className="service-card" style={{
            background: 'white',
            borderRadius: '5px',
            padding: '20px',
            position: 'relative',
            minHeight: '450px',
            paddingBottom: '70px'
          }}>
            <img 
              src={getIsotopeImage(isotope)} 
              alt={isotope.name}
              onError={handleImageError}
              style={{
                width: '100%',
                height: '200px',
                objectFit: 'cover',
                borderRadius: '5px',
                marginBottom: '15px',
                border: '1px solid #ddd'
              }}
            />
            <h3 style={{ color: '#0B2149', marginBottom: '10px', fontSize: '20px' }}>{isotope.name}</h3>
            <p className="half-life" style={{ color: '#666', marginBottom: '10px', fontSize: '14px' }}>
               : {isotope.half_life} 
            </p>
            <p className="decay-type" style={{ color: '#666', marginBottom: '10px', fontSize: '14px' }}>
               : {isotope.decay_type}
            </p>
            <p className="application" style={{ color: '#666', marginBottom: '10px', fontSize: '14px', minHeight: '80px' }}>
              {isotope.application}
            </p>
            
            <div className="card-actions" style={{
              position: 'absolute',
              bottom: '20px',
              left: '20px',
              right: '20px',
              display: 'flex',
              flexDirection: 'column',
              gap: '10px'
            }}>
              <Link 
                to={`/isotopes/${isotope.id}`} 
                className="details-btn"
                style={{ 
                  display: 'block',
                  padding: '8px 15px',
                  backgroundColor: buttonColor,
                  color: 'white',
                  textDecoration: 'none',
                  borderRadius: '3px',
                  textAlign: 'center'
                }}
              >
                
              </Link>
              
              <button 
                type="button"
                className="add-to-calculation-btn"
                onClick={() => handleAddToCart(isotope.id, isotope.name)}
                style={{ 
                  display: 'block',
                  width: '100%',
                  padding: '8px 15px',
                  backgroundColor: buttonColor,
                  color: 'white',
                  border: 'none',
                  borderRadius: '3px',
                  cursor: addingToCart ? 'wait' : 'pointer',
                  textAlign: 'center',
                  fontSize: '14px',
                  opacity: addingToCart ? 0.7 : 1
                }}
                disabled={addingToCart}
              >
                {addingToCart ? '...' : '  '}
                {CONFIG.isGitHubPages && ' ()'}
              </button>
            </div>
          </div>
        ))}
      </div>

      {isotopes.length === 0 && !loading && !error && (
        <div style={{ textAlign: 'center', padding: '40px' }}>
          <p>     </p>
          <p style={{ fontSize: '14px', color: '#666', marginTop: '10px' }}>
               Django : http://localhost:8000/admin/
          </p>
        </div>
      )}
    </div>
  );
};

export default IsotopesList;